/* Reset & Base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
}

/* App Container - LIGHT THEME (default) */
.app {
  min-height: 100vh;
  background: radial-gradient(circle at top, #e5f0ff, #f8fafc 45%, #e2e8f0 100%);
  color: #0f172a;
}

/* Light surfaces */
.skill-card,
.project-card,
.form-input,
.social-link,
.modal-card,
.exp-card {
  background: rgba(255, 255, 255, 0.9);
  border-color: #d1d5db;
}

.section-description,
.project-description,
.modal-description,
.exp-meta {
  color: #4b5563;
}

/* Dark mode root */
.app.dark {
  background: radial-gradient(circle at top, #020617, #020617 40%, #000 100%);
  color: #e2e8f0;
}

.app.dark .navbar.scrolled {
  background: rgba(15, 23, 42, 0.98);
}

.app.dark .skill-card,
.app.dark .project-card,
.app.dark .form-input,
.app.dark .social-link,
.app.dark .modal-card,
.app.dark .exp-card {
  background: rgba(15, 23, 42, 0.9);
  border-color: #1f2937;
}

.app.dark .section-description,
.app.dark .project-description,
.app.dark .modal-description,
.app.dark .exp-meta {
  color: #cbd5e1;
}

/* Navbar */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;
  transition: all 0.3s ease;
}

/* Light navbar when scrolled */
.navbar.scrolled {
  background: rgba(248, 250, 252, 0.9);
  backdrop-filter: blur(12px);
  box-shadow: 0 10px 30px rgba(15, 23, 42, 0.12);
}

/* Dark navbar override */
.app.dark .navbar.scrolled {
  background: rgba(15, 23, 42, 0.95);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.navbar-content {
  max-width: 1280px;
  margin: 0 auto;
  padding: 1rem 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(to right, #22d3ee, #3b82f6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.nav-links {
  display: flex;
  gap: 2rem;
}

/* Nav link colors light vs dark */
.nav-links a {
  color: #0f172a;
  text-decoration: none;
  text-transform: capitalize;
  position: relative;
  transition: color 0.3s ease;
}

.app.dark .nav-links a {
  color: #cbd5e1;
}

.nav-links a:hover {
  color: #22d3ee;
}

.nav-links a::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0;
  height: 2px;
  background: #22d3ee;
  transition: width 0.3s ease;
}

.nav-links a:hover::after {
  width: 100%;
}

/* Theme toggle */
.theme-toggle {
  background: #0f172a;
  color: #e2e8f0;
  border: 1px solid #1e293b;
  border-radius: 9999px;
  padding: 0.35rem 0.85rem;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.theme-toggle:hover {
  background: #1e293b;
  border-color: #22d3ee;
}

/* Hamburger Menu */
.hamburger {
  display: none;
  flex-direction: column;
  gap: 5px;
  background: none;
  border: none;
  cursor: pointer;
  z-index: 60;
}

.hamburger span {
  width: 25px;
  height: 3px;
  background: #22d3ee;
  transition: all 0.3s ease;
  border-radius: 2px;
}

.hamburger span.active:nth-child(1) {
  transform: rotate(45deg) translate(8px, 8px);
}

.hamburger span.active:nth-child(2) {
  opacity: 0;
}

.hamburger span.active:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -7px);
}

/* Mobile Menu */
.mobile-menu {
  position: fixed;
  top: 0;
  right: -100%;
  width: 70%;
  max-width: 300px;
  height: 100vh;
  background: rgba(15, 23, 42, 0.98);
  backdrop-filter: blur(12px);
  padding: 5rem 2rem 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  transition: right 0.3s ease;
  z-index: 55;
  box-shadow: -5px 0 20px rgba(0, 0, 0, 0.5);
}

.mobile-menu.open {
  right: 0;
}

.mobile-menu a {
  color: #cbd5e1;
  text-decoration: none;
  font-size: 1.25rem;
  padding: 0.5rem 0;
  border-bottom: 1px solid #334155;
  transition: color 0.3s ease;
}

.mobile-menu a:hover {
  color: #22d3ee;
}

/* Hero Section */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  position: relative;
  overflow: hidden;
  opacity: 0;
  transition: opacity 1s ease;
}

.hero.visible {
  opacity: 1;
}

.hero-bg {
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.hero-bg-blur {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  filter: blur(80px);
  animation: pulse 4s ease-in-out infinite;
}

.hero-bg-blur-1 {
  top: -50%;
  left: -50%;
  background: rgba(34, 211, 238, 0.1);
}

.hero-bg-blur-2 {
  bottom: -50%;
  right: -50%;
  background: rgba(59, 130, 246, 0.1);
  animation-delay: 2s;
}

.hero-content {
  max-width: 56rem;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 10;
}

.hero-tag {
  display: inline-flex;
  padding: 0.5rem 1rem;
  background: rgba(34, 211, 238, 0.1);
  border: 1px solid rgba(34, 211, 238, 0.3);
  border-radius: 9999px;
  color: #22d3ee;
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 1.5rem;
  animation: fadeIn 0.6s ease-out;
  min-height: 2.5rem;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
}

.cursor {
  animation: blink 1s infinite;
  color: #22d3ee;
}

.hero-title {
  font-size: 3.75rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  animation: slideUp 0.6s ease-out;
}

.gradient-text {
  background: linear-gradient(to right, #22d3ee, #3b82f6, #a855f7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-description {
  font-size: 1.25rem;
  color: #4b5563;
  margin-bottom: 2rem;
  max-width: 42rem;
  margin-left: auto;
  margin-right: auto;
  animation: slideUp 0.6s ease-out 0.2s backwards;
}

.app.dark .hero-description {
  color: #cbd5e1;
}

.hero-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  animation: slideUp 0.6s ease-out 0.4s backwards;
}

/* Tech stack strip */
.tech-strip {
  padding: 1.5rem 1.5rem 0;
}

.tech-strip-inner {
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  justify-content: center;
}

.tech-pill {
  padding: 0.4rem 0.9rem;
  border-radius: 9999px;
  background: rgba(148, 163, 184, 0.2);
  border: 1px solid rgba(148, 163, 184, 0.4);
  font-size: 0.85rem;
  color: #0f172a;
}

.app.dark .tech-pill {
  background: rgba(15, 23, 42, 0.8);
  color: #e5e7eb;
}

/* Buttons */
.btn {
  padding: 0.75rem 2rem;
  border-radius: 0.5rem;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  display: inline-block;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  font-family: inherit;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-primary {
  background: linear-gradient(to right, #22d3ee, #3b82f6);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  box-shadow: 0 10px 40px rgba(34, 211, 238, 0.5);
  transform: scale(1.05);
}

.btn-secondary {
  background: #e5e7eb;
  border: 1px solid #cbd5e1;
  color: #0f172a;
}

.btn-secondary:hover {
  background: #d1d5db;
  transform: scale(1.05);
}

.app.dark .btn-secondary {
  background: #1e293b;
  border-color: #334155;
  color: white;
}

.app.dark .btn-secondary:hover {
  background: #334155;
}

/* Sections */
.section {
  padding: 5rem 1.5rem;
  opacity: 0;
  transform: translateY(40px);
  transition: all 1s ease;
}

.section.visible {
  opacity: 1;
  transform: translateY(0);
}

.section-content {
  max-width: 1280px;
  margin: 0 auto;
}

.contact-content {
  max-width: 56rem;
  text-align: center;
}

.section-title {
  font-size: 2.25rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  text-align: center;
}

/* Skills Grid */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
}

.skill-card {
  backdrop-filter: blur(8px);
  border-radius: 0.75rem;
  padding: 1.5rem;
  transition: all 0.3s ease;
}

.skill-card:hover {
  border-color: rgba(34, 211, 238, 0.5);
  transform: scale(1.05);
}

.skill-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.skill-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #22d3ee;
  margin-bottom: 0.75rem;
}

.skill-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.skill-tag {
  padding: 0.25rem 0.75rem;
  background: rgba(148, 163, 184, 0.3);
  border-radius: 9999px;
  font-size: 0.875rem;
  color: #0f172a;
}

.app.dark .skill-tag {
  background: rgba(51, 65, 85, 0.5);
  color: #cbd5e1;
}

/* Projects Grid */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1.5rem;
}

.project-card {
  backdrop-filter: blur(8px);
  border-radius: 0.75rem;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
}

.project-card:hover {
  border-color: rgba(34, 211, 238, 0.5);
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(15, 23, 42, 0.25);
}

.project-screenshot {
  position: relative;
  width: 100%;
  height: 200px;
  overflow: hidden;
  background: #e5e7eb;
}

.app.dark .project-screenshot {
  background: #1e293b;
}

.project-screenshot img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.project-card:hover .project-screenshot img {
  transform: scale(1.1);
}

.project-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(to top, rgba(15, 23, 42, 0.9), transparent);
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding: 1rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.project-card:hover .project-overlay {
  opacity: 1;
}

.project-icon-large {
  font-size: 3rem;
}

.project-content {
  padding: 1.5rem;
}

.project-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.75rem;
  transition: color 0.3s ease;
}

.project-card:hover .project-title {
  color: #22d3ee;
}

.project-description {
  margin-bottom: 1rem;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-height: 1.6;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.project-tag {
  padding: 0.25rem 0.75rem;
  background: rgba(148, 163, 184, 0.3);
  border-radius: 9999px;
  font-size: 0.875rem;
  color: #1d4ed8;
}

.app.dark .project-tag {
  background: rgba(51, 65, 85, 0.5);
  color: #22d3ee;
}

.project-links {
  display: flex;
  gap: 0.75rem;
}

.project-link,
.project-details {
  background: none;
  border: none;
  color: #2563eb;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: color 0.3s ease;
  text-decoration: none;
  font-family: inherit;
}

.project-link:hover {
  color: #1d4ed8;
}

.project-details {
  color: #64748b;
}

.project-details:hover {
  color: #111827;
}

.app.dark .project-link {
  color: #22d3ee;
}

.app.dark .project-link:hover {
  color: #67e8f9;
}

.app.dark .project-details {
  color: #94a3b8;
}

.app.dark .project-details:hover {
  color: white;
}

/* Experience & Education */
.exp-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 1.5rem;
}

.exp-column {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.exp-heading {
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
  color: #0f172a;
}

.app.dark .exp-heading {
  color: #e5e7eb;
}

.exp-card {
  border-radius: 0.75rem;
  border: 1px solid #d1d5db;
  padding: 1.25rem 1.5rem;
}

.app.dark .exp-card {
  border-color: #1f2937;
}

.exp-role {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.exp-points {
  list-style: disc;
  padding-left: 1.25rem;
  font-size: 0.95rem;
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

/* Contact Form */
.contact-form {
  max-width: 600px;
  margin: 0 auto 3rem;
  text-align: left;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-input {
  width: 100%;
  padding: 1rem;
  border-radius: 0.5rem;
  color: #0f172a;
  font-size: 1rem;
  font-family: inherit;
  transition: all 0.3s ease;
}

.app.dark .form-input {
  color: white;
}

.form-input:focus {
  outline: none;
  border-color: #22d3ee;
  box-shadow: 0 0 0 3px rgba(34, 211, 238, 0.1);
}

.form-input::placeholder {
  color: #64748b;
}

textarea.form-input {
  resize: vertical;
  min-height: 120px;
}

.form-success {
  margin-top: 1rem;
  color: #16a34a;
  font-weight: 500;
  animation: fadeIn 0.3s ease-out;
}

.form-error {
  margin-top: 1rem;
  color: #dc2626;
  font-weight: 500;
  animation: fadeIn 0.3s ease-out;
}

/* Social Links */
.social-links {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

.social-link {
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  text-decoration: none;
}

.social-link:hover {
  background: #22d3ee;
  border-color: #22d3ee;
  transform: scale(1.1);
}

.social-icon {
  width: 1.5rem;
  height: 1.5rem;
  color: #0f172a;
  transition: color 0.3s ease;
}

.app.dark .social-icon {
  color: #cbd5e1;
}

.social-link:hover .social-icon {
  color: white;
}

/* Modal */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  animation: fadeIn 0.3s ease-out;
}

.modal-card {
  border-radius: 1rem;
  max-width: 50rem;
  width: 100%;
  position: relative;
  animation: scaleIn 0.3s ease-out;
  max-height: 90vh;
  overflow-y: auto;
}

.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 2rem;
  height: 2rem;
  background: rgba(148, 163, 184, 0.8);
  border: none;
  border-radius: 50%;
  color: white;
  font-size: 1.25rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease;
  z-index: 10;
}

.modal-close:hover {
  background: #475569;
}

.modal-screenshot {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.modal-content {
  padding: 2rem;
}

.modal-icon {
  width: 4rem;
  height: 4rem;
  border-radius: 0.75rem;
  background: linear-gradient(to right, #a855f7, #ec4899);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

.modal-title {
  font-size: 1.875rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.modal-description {
  font-size: 1.125rem;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.modal-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.modal-tag {
  padding: 0.5rem 1rem;
  background: rgba(148, 163, 184, 0.3);
  border-radius: 9999px;
  color: #1d4ed8;
}

.app.dark .modal-tag {
  background: rgba(51, 65, 85, 0.5);
  color: #22d3ee;
}

.modal-btn {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(to right, #22d3ee, #3b82f6);
  border-radius: 0.5rem;
  color: white;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
}

.modal-btn:hover {
  box-shadow: 0 10px 40px rgba(34, 211, 238, 0.5);
}

/* Footer */
.footer {
  padding: 2rem 1.5rem;
  border-top: 1px solid #d1d5db;
}

.app.dark .footer {
  border-top-color: #1e293b;
}

.footer p {
  max-width: 1280px;
  margin: 0 auto;
  text-align: center;
  color: #64748b;
}

.app.dark .footer p {
  color: #94a3b8;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

@keyframes blink {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

/* Responsive */
@media (max-width: 1024px) {
  .navbar-content {
    padding-inline: 1rem;
  }

  .nav-links {
    gap: 1.25rem;
  }
}

@media (max-width: 768px) {
  .nav-links {
    display: none;
  }

  .hamburger {
    display: flex;
  }

  .hero-title {
    font-size: 2.5rem;
  }

  .hero-description {
    font-size: 1rem;
  }

  .section-title {
    font-size: 1.875rem;
  }

  .skills-grid,
  .projects-grid,
  .exp-grid {
    grid-template-columns: 1fr;
  }

  .hero-tag {
    font-size: 0.875rem;
  }

  .modal-screenshot {
    height: 200px;
  }

  .modal-content {
    padding: 1.5rem;
  }

  .tech-strip {
    padding-inline: 1rem;
  }
}
